export function disinteractLintener(map) {
  window.disinteract = 0
    // map.on('ruler.on', function () {
    //   //if (inspect.isInspecting) inspect.inspectingOff();
    //   console.log("ruler on");
    //   window.disinteract++;
    // });
    // map.on('ruler.off', function () {
    //   console.log("ruler off");
    //   window.disinteract--;
    // });
    map.on('inspect.on', function () {
      //if (maptools.ruler.isMeasuring) maptools.ruler.measuringOff();
      console.log("inspect on");
      window.disinteract++;
    });
    map.on('inspect.off', function () {
      console.log("inspect off");
      window.disinteract--;
    });
    map.on('maptools.on', function () {
      console.log("maptools on");
      window.disinteract++;
    });
    map.on('maptools.off', function () {
      console.log("maptools off");
      window.disinteract--;
    });
} 

export function home() {
  // reset zoom and map center
  window.resize = 1;
  window.mapcenter = {"lng":101.6673626,"lat":13.2808669};
  window.map.flyTo({
    center: {"lng":101.6673626,"lat":13.2808669},
    offset: [window.map_Xoffset, window.map_Yoffset],
    zoom : 4.8, 
    speed : flyspeed, 
    curve : 1, 
    essential: true
  });
} 

export function switchDatapanel(op) {
    if (window.datapanel_isopen !== -1) {
      switch(op) {  
        case 0:
          // map layout
          window.datapanel_isopen = 0;
          break;
        case 1:
          // map&table layout
          window.datapanel_isopen = 1;
          datapanel = {xw:0.4,xh:0.4};
          break;
        case 2:
          // table layout
          window.datapanel_isopen = 2;
          datapanel = {xw:0.55,xh:0.75};
          break;
        default:
          console.log("layout type is unrecognized!")
          window.datapanel_isopen = !window.datapanel_isopen;
      }
  
      for (let m=0;m<3;m++) {
        if (m === op) {
          document.getElementById("headermenu"+m).style.backgroundColor = "#2a58c3";
          document.getElementById("headermenu"+m).style.color = "#fff";
        } else {
          document.getElementById("headermenu"+m).style.backgroundColor = "#fff";
          document.getElementById("headermenu"+m).style.color = "#2a58c3";
        }
      }
  
      //window.datapanel_isopen = !window.datapanel_isopen;
      resizeAdjust()  
      // console.log(window.mapcenter,window.map_Xoffset,window.map_Yoffset)
      window.map.flyTo({
        center: window.mapcenter, //[101.6673626,13.2808669],
        offset: [window.map_Xoffset, window.map_Yoffset],
        //zoom : zoom(z), 
        speed : flyspeed, 
        curve : 1, 
        essential: true
      });    
    }
  
}

