export function switchStyleMenu(sw) {
  //console.log("switchStyleMenu : ",sw);
  if (sw) {
    document.getElementById("map_style_menu_outline").style.display = "block";
  } else {
    document.getElementById("map_style_menu_outline").style.display = "none";
  }
}

export function resizeAdjust() {
  window.resize = 1;
  if($("#wholecontent").width() < 768) {
    document.getElementById("header-menu-container-wrap").classList.add('-phone-layout');
    document.getElementsByClassName("mapboxgl-ctrl-bottom-left")[0].classList.add('-phone-layout');
    document.getElementById("title-container").style.visibility = "hidden";
    document.getElementById("header-menu-container").style.width = "100vw";
    document.getElementById("header-menu-container-wrap").style.width = "100vw";
    document.getElementById("data-panel").style.width = "100%";
    if (window.datapanel_isopen>0) {
      document.getElementById("data-panel").style.display = "block";  
      document.getElementById("vertical-table-container-MAP").style.height = "57px";
      document.getElementsByClassName("mapboxgl-ctrl-bottom-right")[0].style.bottom = ($("#wholecontent").height()*datapanel.xh+60).toString()+"px";
      document.getElementById("data-panel").style.height = (window.datapanel_isopen===1) ?  ($("#wholecontent").height()*datapanel.xh)+"px" : ($("#wholecontent").height()-300).toString()+"px";
      // document.getElementById("data-panel").style.height = ($("#wholecontent").height()*datapanel.xh)+"px";
      window.map_Yoffset = -$("#data-panel").height()/2+10;// - 20;
      // console.log(window.map_Yoffset);
    } else {
      document.getElementById("data-panel").style.display = "none";
      document.getElementById("vertical-table-container-MAP").style.height = "60px";
      document.getElementsByClassName("mapboxgl-ctrl-bottom-right")[0].style.bottom = "75px";
      window.map_Yoffset = -20; //-40;
    }
    window.map_Xoffset = 0
    document.getElementById("notification-container-right").style.display = "none";
    document.getElementById("notification-container-bottom").style.display = "block";
    $("#data-panel").appendTo("#notification-container-bottom");
    if (orientation === 0) {
      // console.log(window.mapcenter,window.map_Xoffset,window.map_Yoffset)
      window.map.flyTo({
        center: window.mapcenter,
        offset: [window.map_Xoffset, window.map_Yoffset],
        essential: true
      });
    }
    orientation = 1;
  } else {
    document.getElementById("header-menu-container-wrap").classList.remove('-phone-layout');
    document.getElementsByClassName("mapboxgl-ctrl-bottom-left")[0].classList.remove('-phone-layout');
    document.getElementById("title-container").style.visibility = "visible";
    document.getElementById("map-container").style.width = "100%";
    document.getElementById("data-panel").style.height = "100%";
    document.getElementById("notification-container-bottom").style.display = "none";
    document.getElementById("notification-container-right").style.display = "block";
    if (window.datapanel_isopen>0) {
      document.getElementById("header-menu-container").style.width = "calc("+(100*(1-datapanel.xw))+"vw - 60px)";
      document.getElementById("header-menu-container-wrap").style.width = "calc("+(100*(1-datapanel.xw))+"vw - 60px)";
      document.getElementById("data-panel").style.display = "block";
      document.getElementById("data-panel").style.width = ($("#wholecontent").width()*datapanel.xw).toString()+"px";
      window.map_Xoffset = -$("#data-panel").width()/2 - 20;
      if (window.datapanel_isopen === 2) document.getElementById("title-container").style.visibility = "hidden";
    } else {
      document.getElementById("header-menu-container").style.width = "calc(100vw - 60px)";
      document.getElementById("header-menu-container-wrap").style.width = "calc(100vw - 60px)";
      document.getElementById("data-panel").style.display = "none";
      window.map_Xoffset = -20;
    }
    window.map_Yoffset = 10; //-30;
    $("#data-panel").appendTo("#notification-container-right");
    $("#lp-button").appendTo("#menu-container-top-right");
    $("#bb-button").appendTo("#menu-container-top-right");
    if (orientation === 1) {
      //console.log("switch to lanscape");
      window.map.flyTo({
        center: window.mapcenter,
        offset: [window.map_Xoffset, window.map_Yoffset],
        essential: true
      });
    }
    orientation = 0;
  }
  window.map.resize();
}

// monitor size change for ui-readjustment
export function addResizeListener() {
  window.addEventListener('resize', resizeAdjust) 
}

export function centerMap(lnglat) {
  window.resize = 1;
  window.mapcenter = lnglat;
  window.map.flyTo({
    center: lnglat,
    offset: [window.map_Xoffset, window.map_Yoffset],
    //zoom : zoom(z), 
    speed : flyspeed, 
    curve : 1, 
    essential: true
  }); 
}

function project(m,d) {
  return m.project(new mapboxgl.LngLat(+d.lon, +d.lat)/*getLL(d)*/);
}

export function markerupdate(m,d) {
  d.attr({
    cx: function(d) { 
      var x = project(m,d).x;
      return x
    },
    cy: function(d) { 
      var y = project(m,d).y;
      return y
    },
  })
}



// ---- Keep track of map center after moving from user ----------------------------------------------
export function trackMapCenter() {
  window.map.on('movestart', function (){
    if (window.resize === 0) {
      move_start = 1;
      startlnglat = window.map.getCenter();
    }
  });
  window.map.on('moveend', function (){  
    if (window.resize === 0 && move_start === 1) {
      window.mapcenter.lng += (window.map.getCenter().lng-startlnglat.lng);
      window.mapcenter.lat += (window.map.getCenter().lat-startlnglat.lat );
      // console.log(window.mapcenter)
    } else {
      setTimeout(function (){
        window.resize = 0; 
        move_start = 0;
      }, 300);    
    }
    // console.log(window.mapcenter)
  });
}

// ---- Relocate some main divs into map-divs for displaying in fullscreen ----
export function moveDivsIntoMap() {

  var removebutton = document.getElementsByClassName('mapboxgl-ctrl-top-left')[0].children;
  // console.log(removebutton);
  for (let i=0;i<removebutton.length;i++) {removebutton[i].parentElement.removeChild(removebutton[i]);}

  $("#notification-container-right").appendTo(".mapboxgl-ctrl-top-right");
  $("#notification-container-bottom").appendTo(".mapboxgl-ctrl-bottom-left");
  $("#menu-container-bottom-right").appendTo(".mapboxgl-ctrl-bottom-right");
  $("#header-container").appendTo(".mapboxgl-ctrl-top-left")
  $("#header-menu-container").appendTo(".mapboxgl-ctrl-top-left")
}


//---- Swipe Detect ---------------------------------------------------------------------------------
function detectswipe(el,func) {
  let swipe_det = new Object();
  swipe_det.sX = 0;
  swipe_det.sY = 0;
  swipe_det.eX = 0;
  swipe_det.eY = 0;
  var min = 40, //required min distance traveled to be considered swipe
  max = 30, // maximum distance allowed at the same time in perpendicular direction
  allowedTime = 1500, // maximum time allowed to travel that distance
  direc, elapsedTime, startTime;
  let ele = document.getElementById(el);
  ele.addEventListener('touchstart',function(e){
    var t = e.touches[0];
    swipe_det.sX = t.screenX; 
    swipe_det.sY = t.screenY;
    swipe_det.eX = 0; 
    swipe_det.eY = 0;    
    direc = "";
    startTime = new Date().getTime(); // record time when finger first makes contact with surface
  },false);
  ele.addEventListener('touchmove',function(e){
    //e.preventDefault();
    var t = e.touches[0];
    swipe_det.eX = t.screenX; 
    swipe_det.eY = t.screenY;    
  },false);
  ele.addEventListener('touchend',function(e){
    elapsedTime = new Date().getTime() - startTime; // get time elapsed
    //console.log(swipe_det.sX,swipe_det.sY,"|",swipe_det.eX,swipe_det.eY);
    //console.log(elapsedTime,swipe_det.eX - swipe_det.sX,swipe_det.eY - swipe_det.sY);
    //horizontal detection
    if (elapsedTime <= allowedTime){
        if ((Math.abs(swipe_det.eX - swipe_det.sX) > min) && (Math.abs(swipe_det.eY - swipe_det.sY) < max)) {
          if(swipe_det.eX > swipe_det.sX) direc = "r"; else direc = "l";
        }
        //vertical detection
        if ((Math.abs(swipe_det.eY - swipe_det.sY) > min) && (Math.abs(swipe_det.eX - swipe_det.sX) < max)) {
          if(swipe_det.eY > swipe_det.sY) direc = "d"; else direc = "u";
        }

        if (direc !== "") {
          if(typeof func === 'function') func(el,direc);
        }
    }
  },false);  
}

function LRactions(el,swipedir) {
    if ((swipedir === 'l' && window.datapanel_isopen === 0)||(swipedir === 'r' && window.datapanel_isopen === 2)) {
      switchDatapanel(1);
    } else {
    if (swipedir === 'l' && window.datapanel_isopen === 1) switchDatapanel(2);
    if (swipedir === 'r' && window.datapanel_isopen === 1) switchDatapanel(0);      
    }

    //console.log(el+" : "+swipedir);
}

function UDactions(el,swipedir) {
    if ((swipedir === 'u' && window.datapanel_isopen === 0)||(swipedir === 'd' && window.datapanel_isopen === 2)) {
      switchDatapanel(1);
    }  else {
    if (swipedir === 'u' && window.datapanel_isopen === 1) switchDatapanel(2);
    if (swipedir === 'd' && window.datapanel_isopen === 1) switchDatapanel(0);      
    }

    //console.log(el+" : "+swipedir);
}

export function addSwipeDetect() {
  detectswipe('notification-container-right',LRactions)
  detectswipe('vertical-table-container-MAP',UDactions)
}

//---- Horizontal scroll by mouse wheel ---------------------------------------------------------------------------------

$.fn.hScroll = function( options )
 {
   function scroll( obj, e )
   {
     var evt = e.originalEvent
     var direction = evt.detail ? evt.detail * (-120) : evt.wheelDelta;
     if( direction > 0) {
        direction =  $(obj).scrollLeft() - 120
     } else {
        direction = $(obj).scrollLeft() + 120
     }
     $(obj).scrollLeft( direction )
     e.preventDefault();
   }
   $(this).width( $(this).find('div').width() )
   $(this).bind('DOMMouseScroll mousewheel', function( e )
   {
    scroll( this, e )
   });
}

