import React from "react"
import {switchDatapanel,home} from '../utils/utils'

function geocoder_searchicon_onclick() {
    let el = document.getElementById("geocoder");
    (el.style.display !== "block") ? el.style.display = "block" : el.style.display = "none";
    document.getElementsByClassName('mapboxgl-ctrl-geocoder--input')[0].focus();
}

function geocoder_searchicon_oncontextmenu() {
    window.geocoder.clear();
    document.getElementsByClassName('mapboxgl-ctrl-geocoder--input')[0].blur();
    // let el = document.getElementById("geocoder");
    // if (el.style.display === "block") el.style.display = "none";
}

function geocoder_focusout() {
    if (document.getElementsByClassName("suggestions")[0].style.display === 'none') {       
        setTimeout(function (){
        document.getElementById("geocoder").style.display = "none";
        }, 200);
    }
}

const HeaderMenu = ()=> {
    return (
        <div id="header-menu-container" className='disable-select'>
            <div id="header-menu-container-wrap">
            <div id="header-menu" align="center">
                <span id="home-toggle-icon"><span className="icon-home" onClick={() => {home()}} onContextMenu={(e)=>{e.preventDefault()}}></span></span>
                <div id="headermenu2" className="header-menu-button header-menu-button-left" onClick={(e) => {switchDatapanel(2);e.preventDefault()}}>ตาราง</div>
                <div id="headermenu1" className="header-menu-button" onClick={() => {switchDatapanel(1)}}>สมดุล</div>
                <div id="headermenu0" className="header-menu-button header-menu-button-right" onClick={() => {switchDatapanel(0)}}>แผนที่</div>
                <span id="geocoder-toggle-icon"><span className="icon-search" onClick={() => {geocoder_searchicon_onclick()}} onContextMenu={(e)=>{geocoder_searchicon_oncontextmenu();e.preventDefault()}}></span></span>
            </div>
            </div>
            <div id="geocoder" className="geocoder" onBlur={() => {geocoder_focusout()}} ></div>
        </div>
    )
}

export default HeaderMenu