import React from "react"

const NotificationBottom = ()=> {
    return (
        <div id='notification-container-bottom' className='map_addon_bottom disable-select'>
            <div id='notification-container-bottom-handle'></div>
            <div id='vertical-table-container-MAP' className= "col-sm-12 height105p">
                <div id="vertical-table-markers-container" className="table-container" onContextMenu={(e)=>{e.preventDefault()}}>
                </div>
            </div>
        </div>
    )
}

export default NotificationBottom