import React from 'react'
import {symbol} from '../markers/constants'


export function addLayers() {
    let pathUrl = 'https://tunchz.github.io/ISOC';//'..';
    window.map.on(window.mapEvent, function () {
        var empty_geojson = {
            'type': 'FeatureCollection',
            'features': []
        };
    
        window.map.addSource('mapth_prov', {
            'type': 'geojson',
            'data': pathUrl + '/json/mapth_prov_2.5p.json'
        });

        window.map.addSource('mapth_dist', {
            'type': 'geojson',
            'data': pathUrl + '/json/mapth_dist_2.5p.json'
        });

        window.map.addSource('mapth_subd', {
            'type': 'geojson',
            'data': pathUrl + '/json/mapth_subd_2.5p_dissolved.json'
        });

        window.map.addSource('mainbasin', {
            'type': 'geojson',
            'data': pathUrl + '/json/mainbasin_10p.json'
        });

        window.map.addSource('subbasin', {
            'type': 'geojson',
            'data': pathUrl + '/json/subbasin_10p.json'
        });

        window.map.addSource('mainriver', {
            'type': 'geojson',
            'data': pathUrl + '/json/main_river.json'
        });

        window.map.addSource('drought_risk', {
            'type': 'geojson',
            'data': pathUrl + '/json/Drought_Risk_34_10p.json'
        });

        window.map.addSource('flood_risk', {
            'type': 'geojson',
            'data': pathUrl + '/json/Flood_Risk_35_10p.json'
        });

        window.map.addSource('water_l', {
            'type': 'geojson',
            'data': pathUrl + '/json/P_Water_L.geojson'
        });

        window.map.addSource('water_m', {
            'type': 'geojson',
            'data': pathUrl + '/json/P_Water_M.geojson'
        });
    
    

        window.map.addLayer({
            'id': 'แจ้งเตือนภัย',
            'type': 'symbol',
            'source': {
                'type': 'geojson',
                'data': empty_geojson
            },
            'layout': {
                'visibility': 'none',
                'text-field': ''
            },
            'paint': {
                'text-color': '#fff',
                'text-opacity':0
            }
        });

        window.map.addLayer({
            'id': 'แจ้งเตือนวางแผน',
            'type': 'heatmap',
            'source': {
                'type': 'geojson',
                'data': empty_geojson
            },
            'layout': {
                'visibility': symbol[1].initialvis
            },
            'paint': {
                'heatmap-color': '#ffffff',
                'heatmap-opacity': 0
            }
        });

        window.map.addLayer({
            'id': 'พื้นที่จังหวัด',
            'type': 'fill',
            'source': 'mapth_prov',
            'layout': {
            'visibility': 'visible'
            },
            'paint': {
            'fill-color': '#ffffff',
            'fill-outline-color': 'white',
            'fill-opacity': 0
            }
        });

        window.map.addLayer({
            'id': 'เขตจังหวัด',
            'type': 'line',
            'source': 'mapth_prov',
            'layout': {
            'visibility': 'visible'
            },
            'paint': {
            'line-color': '#dddddd',
            'line-opacity': 1
            }
        });

        window.map.addLayer({
            'id': 'พื้นที่อำเภอ',
            'type': 'fill',
            'source': 'mapth_dist',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': '#ffffff',
            'fill-outline-color': 'white',
            'fill-opacity': 0
            }
        });

        window.map.addLayer({
            'id': 'เขตอำเภอ',
            'type': 'line',
            'source': 'mapth_dist',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'line-color': '#dddddd',
            'line-opacity': 1
            }
        });

        window.map.addLayer({
            'id': 'พื้นที่ตำบล',
            'type': 'fill',
            'source': 'mapth_subd',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': '#ffffff',
            'fill-outline-color': 'white',
            'fill-opacity': 0
            }
        });

        window.map.addLayer({
            'id': 'เขตตำบล',
            'type': 'line',
            'source': 'mapth_subd',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'line-color': '#bbbbbb',
            'line-opacity': 1
            }
        });

        window.map.addLayer({
            'id': 'พื้นที่ลุ่มน้ำหลัก',
            'type': 'fill',
            'source': 'mainbasin',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': '#ffffff',
            'fill-outline-color': 'white',
            'fill-opacity': 0
            }
        });

        window.map.addLayer({
            'id': 'เขตพื้นที่ลุ่มน้ำหลัก',
            'type': 'line',
            'source': 'mainbasin',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'line-color': '#00f',
            'line-opacity': 1
            }
        });

        window.map.addLayer({
            'id': 'พื้นที่ลุ่มน้ำย่อย',
            'type': 'fill',
            'source': 'subbasin',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': '#ffffff',
            'fill-outline-color': 'white',
            'fill-opacity': 0
            }
        });

        window.map.addLayer({
            'id': 'เขตพื้นที่ลุ่มน้ำย่อย',
            'type': 'line',
            'source': 'subbasin',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'line-color': '#00f',
            'line-opacity': 1
            }
        });      


        window.map.addLayer({
            'id': 'แม่น้ำสายหลัก',
            'type': 'line',
            'source': 'mainriver',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'line-color': '#00f',
            'line-width': 2,
            'line-opacity': 1
            }
        });      

        window.map.addLayer({
            'id': 'พื้นที่เสี่ยงภัยแล้ง',
            'type': 'fill',
            'source': 'drought_risk',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': [
                'case',
                ['==', ['get', 'ระดับ'], '3'], 'rgba(227,116,0,1)',
                ['==', ['get', 'ระดับ'], '4'], 'rgba(197,34,31,1)',
                '#ffffff'
            ],
            // 'fill-outline-color': 'white',
            'fill-opacity': 0.5
            }
        });

        window.map.addLayer({
            'id': 'พื้นที่เสี่ยงน้ำท่วม',
            'type': 'fill',
            'source': 'flood_risk',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': [
                'case',
                ['==', ['get', 'ระดับ'], '3'], 'rgba(100,100,255,1)',
                ['==', ['get', 'ระดับ'], '5'], 'rgba(0,0,255,1)',
                '#ffffff'
            ],
            // 'fill-outline-color': 'white',
            'fill-opacity': 0.5
            }
        });

        window.map.addLayer({
            'id': 'แหล่งน้ำขนาดใหญ่',
            'type': 'fill',
            'source': 'water_l',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': 'rgba(0,0,255,1)',
            // 'fill-outline-color': 'white',
            'fill-opacity': 0.5
            }
        });

        window.map.addLayer({
            'id': 'แหล่งน้ำขนาดกลาง',
            'type': 'fill',
            'source': 'water_m',
            'layout': {
            'visibility': 'none'
            },
            'paint': {
            'fill-color': 'rgba(0,0,255,1)',
            // 'fill-outline-color': 'white',
            'fill-opacity': 0.5
            }
        });

        window.map.addLayer({
            'id': 'บ่อบาดาล (จุด)',
            'type': 'circle',
            'source': {
            'type': 'geojson',
            'data': pathUrl + '/json/well_small.geojson'
            },
            layout: {
                'visibility': 'none'
            },          
            paint: {
                'circle-color': '#0f0',
                'circle-radius': 2,
                'circle-stroke-width': 0,
                'circle-stroke-color': '#fff'
            }
        });

        window.map.addLayer({
            'id': 'จุดความร้อน (จุด)',
            'type': 'circle',
            'source': {
            'type': 'geojson',
            'data': pathUrl + '/json/hotspotth.geojson'
            },
            layout: {
                'visibility': 'none'
            },          
            paint: {
                'circle-color': '#f00',
                'circle-radius': 2,
                'circle-stroke-width': 0,
                'circle-stroke-color': '#fff'
            }
        });
    });
}