import React from "react"
import Modals from './Modals'

const Header = ()=> {
    return (
        <div id='header-container' className='map_addon_top_left disable-select'>
        <div id = "logo-container" className= "col-xs-3" data-toggle="modal" data-target="#info-modal">
            <img id = "logo" src = 'https://tunchz.github.io/ISOC/img/isoc.png' alt="ISOC Logo"></img>
        </div>
        <div id = "title-container" className= "col-xs-9 height100p">
            <div id = "Text1_" className= "col-xs-8 height100p">
                <span>DRM </span>
            </div>
            <div id = "Text2_" className= "col-xs-4 height100p">
                <div type="button" className="btn btn-primary" data-toggle="modal" data-target="#info-modal">
                    <div>?</div>
                </div>                          
            </div>
        </div>
        <Modals />
        </div>
    )
}

export default Header