import React from "react"

const NotificationRight = ()=> {
    return (
        <div id='notification-container-right' className='map_addon_right disable-select'>
            <div id='table-container-MAP' className= "col-sm-12 height100p">
                <div id="table-markers-container" className="table-container" onContextMenu={(e)=>{e.preventDefault()}}></div>
                <div id='menu-container-top-right' className='map_addon_top_right'></div>
            </div>
            <div id="data-panel" className= "col-xs-12 height100p">
                <div id='table-container-BG' className= "col-xs-12 height100p">
                    <div id="table-container" className="table-container" onContextMenu={(e)=>{e.preventDefault()}}></div> 
                </div>                
            </div>
        </div>
    )
}

export default NotificationRight