// ---- attach script tag to document.head ----------------------------------------------------------
function importScript(url, async) {
    const script = document.createElement('script')
    script.src = url
    script.async = (typeof async === 'undefined' ? true : async )
    document.head.appendChild(script)
}

export function importExternalScript() {

let pathUrl = 'https://tunchz.github.io/ISOC';//'.';
importScript(pathUrl + '/js/pace.js')
importScript(pathUrl + '/js/modal.js')

attachDynamicStyle()
}

function attachDynamicStyle() {
    const style = document.createElement('style')
    style.id = "dynamic_style"
    document.head.appendChild(style)
}
