import React, { useRef, useEffect } from 'react'
import {addResizeListener,switchDatapanel,moveDivsIntoMap,trackMapCenter,centerMap,addSwipeDetect,disinteractLintener,addHorizontalScroll} from './utils/utils'
import {styles,importExternalScript} from './utils/constants'

import './css/FWTT.css'
import './css/fonts.css'
import './css/layerControl.css'
import './css/mapbox-gl_v1120.css'
import './css/maptoolscontrol.css';
import './css/mapbox-gl-geocoder.css'
import './css/mapbox-gl-style-switcher.css'
import './css/jquery-ui.css'

import mapboxgl from 'mapbox-gl'
// import 'mapbox-gl/src/css/mapbox-gl.css'

// import MapboxDraw from "@mapbox/mapbox-gl-draw"
// import '@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css'
import MaptoolsControl, {CompassControl} from './maptoolscontrol/maptoolscontrol'

import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder'
// import 'mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css'

import { MapboxStyleSwitcherControl } from "mapbox-gl-style-switcher";
// import 'mapbox-gl-style-switcher/styles.css'

import layerControl from './layerControl/layerControl'
// import './layerControl/layerControl.css'
import {layerControlConfig} from './layerControl/config'
import {addLayers} from './layerControl/addLayers'


import Header from './components/Header'
import HeaderMenu from './components/HeaderMenu'
import NotificationRight from './components/NotificationRight'
import NotificationBottom from './components/NotificationBottom'

import './App.css'

import {markers} from './markers/markers'

import {createBrowserHistory} from 'history';

export const history = createBrowserHistory({
  basename: process.env.PUBLIC_URL
});

let map;
let draw;
let geocoder;

function initialize() {

  markers()
  addLayers()
  
  // monitor size change for ui-readjustment
  addResizeListener()
  // import external javascript library
  importExternalScript()
  // relocate some main divs into map-divs for displaying in map fullscreen
  moveDivsIntoMap()
  // start Datapane with 0: Map-layout | 1: Balance-layout | 2: Table-layout
  switchDatapanel(0)
  // add tracking for map center using in ui interaction map positioning
  trackMapCenter()
  // add event listener for swipe detection to icon notification table both on the right and bottom
  addSwipeDetect()
  // add event listener for horizontal scroll in Portrait mode
  // addHorizontalScroll('#vertical-table-markers-container')

}

function App() {
    let mapRef = useRef(null)

    useEffect(() => {

        mapboxgl.accessToken = 'pk.eyJ1IjoiYnJhdmVjb3ciLCJhIjoiY2o1ODEwdWljMThwbTJ5bGk0a294ZmVybiJ9.kErON3w2kwEVxU5aNa-EqQ'
        map = new mapboxgl.Map({
            container: mapRef.current || '',
            style: 'mapbox://styles/mapbox/satellite-streets-v11',
            // center : [101.6673626,13.2808669],
            center: window.mapcenter,
            minZoom: 0.3,
            zoom : 4.8,
            pitch: 0,
            antialias: true,
            interactive: true,
            hash: true
        });
        window.map = map
        draw = new MaptoolsControl()
        window.draw = draw

        map.once('load', () => {
            initialize()
            geocoder = new MapboxGeocoder({
                accessToken: mapboxgl.accessToken,
                flyTo: true,
                marker: {offset: [-13,-36] ,color: 'red'},
                mapboxgl: mapboxgl
            });
            window.geocoder = geocoder

            geocoder.on('result', function(e) {
                window.resize = 1;
                centerMap({"lng":e.result.center[0],"lat":e.result.center[1]});
            });
            document.getElementById('geocoder').appendChild(geocoder.onAdd(map))
            
            map.addControl(new mapboxgl.ScaleControl(), 'bottom-left');
            let mapscale = document.getElementsByClassName("mapboxgl-ctrl-scale")[0]
            mapscale.id = 'map-scale'
            mapscale.classList.add('disable-select')
            map.addControl(new CompassControl(), 'bottom-left');
            map.doubleClickZoom.disable()
            map.addControl(new MapboxStyleSwitcherControl(styles, 'Satellite'), 'top-left');
            map.addControl(new mapboxgl.FullscreenControl(), 'top-left')
            map.addControl(new mapboxgl.GeolocateControl({positionOptions: {enableHighAccuracy: true},trackUserLocation: true}), 'top-left')
            // map.addControl(new mapboxgl.NavigationControl({showCompass: true,showZoom:false}), 'top-left')
            map.addControl(draw, 'top-left')

            disinteractLintener(map);


            setTimeout(function (){

              if (window.run > 0) {
                // var removebutton = document.getElementsByClassName('mgl-layerControl');
                // for (i=0;i<removebutton.length;i++) {removebutton[i].parentElement.removeChild(removebutton[i]);}   
                map.addControl(new layerControl(layerControlConfig()), "top-left");
                window.run++;          
              }

              window.disinteract = 0;
      
            }, 3000);



        });
    }, []);

    return (
        <div id="wholecontent">
            <div id="map-container">
                <div id="map" ref={mapRef} />
                <Header />
                <HeaderMenu />
                <NotificationRight />
                <NotificationBottom />
            </div>
        </div>
    );
}

export default App;
