export function markers() {
  // console.log(data_thaiwater)
  // console.log(data_drm)
  // console.log(mapprov_geojson)

  map_geojson = mapprov_geojson
  drm_geojson = {"type": "FeatureCollection"};
  (async() => {
    while(typeof data_drm === "undefined" || typeof data_thaiwater === "undefined") // define the condition as you like
      await new Promise(resolve => setTimeout(resolve, 100));

    // ---- Run this after data_drm and data_thaiwater are ready!!!
    // console.log('data loaded!!!', data_drm, data_thaiwater)
    drm_geojson.features = data_drm.features.concat(data_thaiwater.features);//[...data_drm,...data_thaiwater];//
    // console.log(drm_geojson.features)
    var plan = {},noti = {};
    var drm = [];     
    riskplan_list.forEach(function (item, index) {
        plan[item] = {"type": "FeatureCollection"};
        plan[item].features = [];
    });

    risknoti_list.forEach(function (item, index) {
        noti[item] = {"type": "FeatureCollection"};
        noti[item].features = [];
    });

    drm_geojson.features.forEach(function (item, index) {
        var disaster_type_id = item.properties.disaster_type_id;
        item.properties['icon'] = symbol[disaster_type_id].icon;
        item.properties['icon_url'] = symbol[disaster_type_id].icon_url;
        if (!item.properties.color) item.properties['color'] = symbol[disaster_type_id].color;

        if (riskplan_list.includes(disaster_type_id)) {
            let prov_id = item.properties.val.toString();
            let prov_rec = ($.grep(map_geojson.features,function (n,i){return n.properties.PROVINCE_C === prov_id;}))[0];
            // console.log(mapprov_geojson.features.filter(function(item) {return item.properties.PROVINCE_C == prov_id;}));
            // prov_rec = mapprov_geojson.features.filter(function(item) {return item.properties.PROVINCE_C == prov_id;})[0];
            // console.log(disaster_type_id,prov_id,prov_rec);
            try {
              item.geometry = prov_rec.geometry;
              item.properties.val = prov_rec.properties.จังหวัด;
              item.properties['opacity'] = symbol[disaster_type_id].opacity;
              item.properties['center'] = getCenter(item.geometry.coordinates);
              // console.log(disaster_type_id,item)
              plan[disaster_type_id].features.push(item);              
            } catch(err) {
              console.log(err)
            }
            


        } else if (risknoti_list.includes(disaster_type_id)) {
            noti[disaster_type_id].features.push(item);
            item.properties['center'] = {lng:item.properties.lon,lat:item.properties.lon}
        }
        drm.push(item.properties);
    });

    // console.log("plan",plan);
    // console.log("noti",noti);

    Object.keys(plan).forEach(function (item) {
        // console.log(plan[item].features.length);
        if (plan[item].features.length > 0) map_add_polygon(plan[item],symbol[item].layername,symbol[item].initialvis);
    });
    Object.keys(noti).forEach(function (item) {
        if (noti[item].features.length > 0) map_add_custommarker(noti[item],symbol[item].layername,symbol[item].marker_url,symbol[item].color,icon_zoom.unzoomed,45,symbol[item].initialvis);
    });



    // console.log(drm);

    display_table_markers(drm);

    setTimeout(function (){

        // var removebutton = document.getElementsByClassName('mgl-layerControl');
        // for (let i=0;i<removebutton.length;i++) {removebutton[i].parentElement.removeChild(removebutton[i]);}

        window.map.setStyle('mapbox://styles/mapbox/satellite-v9');
        window.run = 1;
    }, 1000);



  })();





}

export function filterNotiType(type,filter) {
  // var noti = {'แจ้งเตือนภัย':'disaster',
  //             'แจ้งเตือนวางแผน':'plan',
  //             'เขื่อน':'dam',
  //             'ระดับน้ำ':'waterlevel',
  //             'คุณภาพน้ำ':'waterquality',
  //             'ฝน':'rain',
  //             'คาดการณ์ฝน':'rainforecast',
  //             };
  //console.log(type,filter);
  //disaster_risk_list_summary[type].filterExact(!filter);
  if (typeof type !== 'undefined') {
    if (!filter) {
      NotiType_filteredSet = NotiType_filteredSet.filter(function(value, index, arr){ 
        return value !== type;
      });    
    } else {
      NotiType_filteredSet.push(type);
    }
  }
  disaster_risk_list_summary.notitype.filter(function(d) { return NotiType_filteredSet.includes(d); });
  disaster_risk_list.notitype.filter(function(d) { return NotiType_filteredSet.includes(d); });

  console.log(NotiType_filteredSet);

  try {
    var removetable = document.getElementById('table_image');
    removetable.parentElement.removeChild(removetable); 
  } catch(err) {
    console.log("no table_image_marker exists!")
  }
  try {
    var removetable = document.getElementById('table_image_marker');
    removetable.parentElement.removeChild(removetable);
  } catch(err) {
    console.log("no table_image exists!")
  }
  try {
    var removetable = document.getElementById('vertical_table_image_marker');
    removetable.parentElement.removeChild(removetable);
  } catch(err) {
    console.log("no vertical_table_image_marker exists!")
  }


  // var removetable = document.getElementById('table_image');
  // removetable.parentElement.removeChild(removetable);    
  // removetable = document.getElementById('table_image_marker');
  // removetable.parentElement.removeChild(removetable);
  // removetable = document.getElementById('vertical_table_image_marker');
  // removetable.parentElement.removeChild(removetable);

  tabulateimg(disaster_risk_list.dtype_id.bottom(Infinity), ["icon_url","disaster_type","source","updated_date","DRM_state","level_detail","attr","val","response","contract","color"]);

  tabulateimg_marker(disaster_risk_list_summary.dtype_id.bottom(Infinity), ["icon_url","num_rec","blank","disaster_type","blank","blank","blank","blank","blank","color"]);
  vertabulateimg_marker(disaster_risk_list_summary.dtype_id.bottom(Infinity), ["icon_url","num_rec","blank","disaster_type","blank","blank","blank","blank","blank","color"]);

  return disaster_risk_list.dtype_id.bottom(Infinity).length;
}



function getCenter(coords) {
    //console.log(coords);
    var bounds = {}, coords, point, latitude, longitude,center;
  
      for (var j = 0; j < coords.length; j++) {
        for (var k = 0; k < coords[j].length; k++) {
          longitude = coords[j][k][0];
          latitude = coords[j][k][1];
          bounds.xMin = bounds.xMin < longitude ? bounds.xMin : longitude;
          bounds.xMax = bounds.xMax > longitude ? bounds.xMax : longitude;
          bounds.yMin = bounds.yMin < latitude ? bounds.yMin : latitude;
          bounds.yMax = bounds.yMax > latitude ? bounds.yMax : latitude;
        }
      }
    
    return {"lng":(bounds.xMin+bounds.xMax)/2, "lat":(bounds.yMin+bounds.yMax)/2};
}

function map_add_polygon(map_geojson,layername,visibility) {
    //var layername = 'map_th_prov'
    window.map.on(window.mapEvent, function () {
      window.map.addSource(layername, { type: 'geojson', data: map_geojson });
      window.map.addLayer({
        'id': layername,
        'type': 'fill',
        'source': layername,
          layout: {
              'visibility': visibility
          },
        'paint': {
          'fill-color': ['get', 'color'],
          'fill-opacity': ['get', 'opacity']
        }
      });
      window.map.addLayer({
        'id': layername+'_bound',
        'type': 'line',
        'source': layername,
          layout: {
              'visibility': visibility
          },
        'paint': {
          'line-width': 2,
          'line-color': '#fff',
          'line-opacity': 0
        }
      });

      window.map.addLayer({
          id: layername+'_label',
          type: "symbol",
          source: layername,
          layout: {
              'visibility': visibility,
              "text-field": ['get','val'],//"{PROVINCE_N}\n",
              "text-font": ['Open Sans Extrabold', 'Arial Unicode MS Bold'],//["Droid Sans Regular"],
              "text-size": 10,
              'symbol-placement': "point"
          },
          paint: {
              "text-color": '#fff', //["case",["boolean", ["feature-state", "hover"], false],'rgba(255,0,0,0.75)','rgba(0,0,0,0.75)'],
              "text-halo-color": ['get','color'], //["case",["boolean", ["feature-state", "hover"], false],'rgba(255,255,0,0.75)','rgba(255,255,255,0.75)'],
              "text-halo-width": 0.5,
              "text-halo-blur": 0,
          }
      });

      // //Filter map layer
      // window.map.setFilter('th_prov_bound',["in", "PROVINCE_C", '63','50'])


      if (window.run === 1) {
        
        var popup = new mapboxgl.Popup(/*{
          offset: 10,
          closeButton: false,
          closeOnClick: false
        }*/);

           
        window.map.on('click', layername, function (e) {
          if (window.disinteract === 0) {
            window.map.getCanvas().style.cursor = 'pointer';
            //var coordinates = e.features[0].geometry.coordinates.slice();
            var a1 = e.features[0].properties.disaster_type
            var a2 = e.features[0].properties.จังหวัด;
             popup
              .setLngLat(e.lngLat)
              .setHTML(
                  'ภัย : ' + a1 + '<br>จังหวัด : ' + a2
              )
              .addTo(window.map);
            //console.log(e.features[0].properties.disaster_type_id,e.features[0].properties.disaster_id);
            switchUnselectVisibility(e.features[0].properties.disaster_type_id,e.features[0].properties.disaster_id);
            if (datapanel_isopen === 0) switchDatapanel(1);

            //console.log(JSON.parse(e.features[0].properties.center));
            centerMap(JSON.parse(e.features[0].properties.center));
          }
        });

        window.map.on('mouseenter', layername, function () {
          if (window.disinteract === 0) {
            if (window.map.getCanvas().style.cursor !== 'pointer') {
              previous_cursor = window.map.getCanvas().style.cursor;
              window.map.getCanvas().style.cursor = 'pointer';            
            }
          }
        });

        window.map.on('mouseleave', layername, function () {
          if (window.disinteract === 0) {
            window.map.getCanvas().style.cursor = previous_cursor;
            // popup.remove();
          }
        });

        window.map.on('contextmenu', layername, function (e) {
          if (window.disinteract === 0) {
          switchUnselectVisibility(e.features[0].properties.disaster_type_id);
            window.map.flyTo({
              center: [101.6673626,13.2808669],
              offset: [window.map_Xoffset, window.map_Yoffset],
              zoom : 4.8, 
              speed : flyspeed, 
              curve : 1, 
              essential: true
            });
          }
        });
      }
    });

  //console.log("map done")
}

function map_add_custommarker(datageojson,layername,imageurl,textcolor,size,offset,visibility) {
    //console.log(datageojson);
    window.map.on(window.mapEvent, function () {
        window.map.loadImage(imageurl,
            function (error, image) {
                if (error) throw error;
                window.map.addImage(layername+'-icon', image);
                window.map.addSource(layername, {
                    'type': 'geojson',
                    'data': datageojson
                });
                window.map.addLayer({
                    'id': layername,
                    'type': 'symbol',
                    'source': layername,
                    'layout': {
                        'visibility': visibility,
                        'icon-image': layername+'-icon',
                        'icon-size': size,
                        'icon-anchor': 'bottom',
                        'icon-allow-overlap': true
                    },
                    'paint': {
                      'icon-opacity': 1
                    }
                });
                // if (true) {
  
                window.map.addLayer({
                    'id': layername+'_label',
                    'type': 'symbol',
                    'source': layername,
                    'layout': {
                        'visibility': visibility,
                        'text-field': '',//['get', 'val'],
                        'text-font': ['Roboto Black', 'Arial Unicode MS Bold'],
                        'text-ignore-placement': true,
                        'text-offset': [0, -4.9],
                        'text-size': 8
                    },
                    'paint': {
                        'text-color': textcolor,
                        'text-opacity': 1,
                        'text-halo-color': "rgba(255,255,255,1)",                  
                        'text-halo-width': 1,
                        'text-halo-blur': 0
                    }
                });
  
                // }
            }
        );
  
  
      if (window.run === 1) {
        var popup = new mapboxgl.Popup({
          offset: offset,
          closeButton: false,
          closeOnClick: false
        });
  
        window.map.on('click', layername, function (e) {
          if (window.disinteract === 0) {
            var coor = e.features[0].geometry.coordinates.slice();
            var a1 = e.features[0].properties.disaster_type
            var a2 = e.features[0].properties.level_detail;
            //console.log(a1,a2)
            switchUnselectVisibility(e.features[0].properties.disaster_type_id,e.features[0].properties.disaster_id);
            if (datapanel_isopen === 0) switchDatapanel(1);
            //console.log(e.features[0].geometry.coordinates.slice());
            centerMap({"lng":coor[0],"lat":coor[1]});
          }
        });
  
        window.map.on('mouseenter', layername, function (e) { 
          if (window.disinteract === 0) {
            if (window.map.getCanvas().style.cursor !== 'pointer') {
              previous_cursor = window.map.getCanvas().style.cursor;
              window.map.getCanvas().style.cursor = 'pointer';            
            }
  
            var coordinates = e.features[0].geometry.coordinates.slice();
            var a1 = e.features[0].properties.disaster_type
            var a2 = e.features[0].properties.level_detail;
            var a3 = e.features[0].properties.attr
            var a4 = e.features[0].properties.val;
             popup
              .setLngLat(coordinates)
              .setHTML(
                  'ภัย : ' + a1 + '<br>ระดับ : ' + a2 + '<br>'+ a3 +' : ' + a4
              )
              .addTo(window.map);
          }
        });
  
        window.map.on('mouseleave', layername, function () {
          if (window.disinteract === 0) {
            window.map.getCanvas().style.cursor = previous_cursor;
            popup.remove();
          }
        });
  
        window.map.on('contextmenu', layername, function (e) {
          if (window.disinteract === 0) {
            switchUnselectVisibility(e.features[0].properties.disaster_type_id);
            window.map.flyTo({
              center: [101.6673626,13.2808669],
              offset: [window.map_Xoffset, window.map_Yoffset],
              zoom : 4.8, 
              speed : flyspeed, 
              curve : 1, 
              essential: true
            });     
          }  
        });
      }
    });
  
    //console.log("layer", layername)
}


