import React from 'react'
import $ from "jquery"

export const data_drm = get_geojson('./json/DRM.json')

function get_geojson(url) {
    var drm_geojson = {"type": "FeatureCollection"};
    $.getJSON(url, function(data) {
        drm_geojson.features = [...data.features];
        
    });
    return drm_geojson;
    // return drm_geojson.features;
}

  